// slider = document.querySelector('.hero-container'), // Ð¾ÑÐ½Ð¾Ð²Ð½Ñ‹Ð¹ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚ Ð±Ð»Ð¾ÐºÐ°

const sliderItems = document.querySelectorAll('.slider-item'), // ÐºÐ¾Ð»Ð»ÐµÐºÑ†Ð¸Ñ .slider-item
  sliderControl = document.querySelector('.slider-control'), // Ð±Ð»Ð¾Ðº ÐµÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð¾Ð² ÑƒÐ¿Ñ€Ð°Ð²Ð»ÐµÐ½Ð¸Ñ
  sliderButtons = document.querySelectorAll('.slider-control-button'), // ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ñ‹ ÑƒÐ¿Ñ€Ð°Ð²Ð»ÐµÐ½Ð¸Ñ
  sliderButtonPrev = document.querySelector('.btn-prev'), // ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚ ÑƒÐ¿Ñ€Ð°Ð²Ð»ÐµÐ½Ð¸Ñ 'ÐÐ°Ð·Ð°Ð´'
  sliderButtonNext = document.querySelector('.btn-next'), // ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚ ÑƒÐ¿Ñ€Ð°Ð²Ð»ÐµÐ½Ð¸Ñ 'Ð’Ð¿ÐµÑ€ÐµÐ´'
  indicatorItems = document.querySelectorAll('.slider-nav-button'); // ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ñ‹ ÑƒÐ¿Ñ€Ð°Ð²Ð»ÐµÐ½Ð¸Ñ

let index = 0;

const activeSlide = n => {
  for (slide of sliderItems) {
    slide.classList.remove('active');
  }
  sliderItems[n].classList.add('active');
};

const currentIndicator = n => {
  for (indicator of indicatorItems) {
    indicator.classList.remove('current');
  }
  indicatorItems[n].classList.add('current');
};

const prepareCurrentSlide = ind => {
  activeSlide(ind);
  currentIndicator(ind);
};

//Ñ„ÑƒÐ½ÐºÑ†Ð¸Ñ Ð¿ÐµÑ€ÐµÐºÐ»ÑŽÑ‡ÐµÐ½Ð¸Ðµ Ð½Ð° ÑÐ»ÐµÐ´ÑƒÑŽÑ‰Ð¸Ð¹ ÑÐ»Ð°Ð¹Ð´
const nextSlide = () => {
  if (index == sliderItems.length - 1) {
    index = 0;
    prepareCurrentSlide(index);
  } else {
    index++;
    prepareCurrentSlide(index);
  }
};

//Ñ„ÑƒÐ½ÐºÑ†Ð¸Ñ Ð¿ÐµÑ€ÐµÐºÐ»ÑŽÑ‡ÐµÐ½Ð¸Ðµ Ð½Ð° Ð¿Ñ€ÐµÐ´Ñ‹Ð´ÑƒÑ‰Ð¸Ð¹ ÑÐ»Ð°Ð¹Ð´
const prevSlide = () => {
  if (index == 0) {
    index = sliderItems.length - 1;
    prepareCurrentSlide(index);
  } else {
    index--;
    prepareCurrentSlide(index);
  }
};

indicatorItems.forEach((item, indexIndicator) => {
  item.addEventListener('click', () => {
    index = indexIndicator;
    prepareCurrentSlide(index);
  });
});

sliderControl.addEventListener('click', function (e) {
  const target = e.target;
  Array.from(sliderButtons).forEach(item => {
    item.classList.remove('current');
  });
  target.classList.add('current');
});

sliderButtonNext.addEventListener('click', nextSlide);
sliderButtonPrev.addEventListener('click', prevSlide);

const interval = setInterval(nextSlide, 5000);
